@import url(https://fonts.googleapis.com/css2?family=Epilogue&display=swap);
@import url(https://cdn.jsdelivr.net/npm/pretty-checkbox@3.0/dist/pretty-checkbox.min.css);
* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}

body {
  background-color: #e5e7e9;
  margin: 0;
  font-family: 'Epilogue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

input, textarea {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 12px 16px;
  border: 1px solid #cecece;
  font-size: 1.0em;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

}

button, .button {
  padding: 12px 16px;
  background: #036B52;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 2px;
  color: #fff;
  font-size: 1.0em;
  text-transform: uppercase;
}

button:active, button:hover, .button:hover {
  background: #023031;
}

button:disabled {
  background: #e4e4e4;
  color:#303030
}

.page {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}



code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.loading {
  align-items: center;
  display: flex;
  justify-content: center;
}

.load-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.c-loader {
  -webkit-animation: is-rotating 1s infinite;
          animation: is-rotating 1s infinite;
  border: 10px solid #6a6a6a;
  border-radius: 50%;
  border-top-color: #048c6d;
  height: 100px;
  width: 100px;
  margin-bottom: 10px;
}

.load-box-header {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.c-loader-header {
  -webkit-animation: is-rotating 1s infinite;
          animation: is-rotating 1s infinite;
  border: 5px solid #e5e5e5;
  border-radius: 50%;
  border-top-color: #048c6d;
  height: 30px;
  width: 30px;
  margin-bottom: 10px;
}

.load-text-black{
  color:#6a6a6a;
}

@-webkit-keyframes is-rotating {
  to {
    -webkit-transform: rotate(1turn);
            transform: rotate(1turn);
  }
}

@keyframes is-rotating {
  to {
    -webkit-transform: rotate(1turn);
            transform: rotate(1turn);
  }
}

.login-page {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.logo {
  content: url(/trybetunes/static/media/logo_login.2e748cfc.png);
  margin: 30px 0;
}


.login {
  width: 697px;
  height: 314px;

  background: #ffffff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 10px;

  display: flex;
  flex-wrap: nowrap;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.login input , .login button {
  width: 90%;
  margin: 10px auto;
}

@media screen and (max-width: 700px) {
  .login {
    width: 90%
  }

}
header {
  background-color: #023031;
}

.header-top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 5%;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.logo-topo {
  content: url(data:image/png;base64,);
  margin: 20px 0 30px 0;
}

.header-user {
  background-color: #fefefe;
  display: flex;
  flex-direction: row;
  align-items: center;
  border-radius: 20px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.header-user:hover {
  background-color: #e5e7e9;
}

.profile-image {
  width: 50px;
  border-radius: 60%;
}

.profile-name {
  padding: 0 30px; 
}

.menu {
  width: 100%;
  display: flex;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);


}

.menu-item {
  width: 100%;
  padding: 25px;
  background: #F0F2F5;
  text-align: center;
  border: 1px solid #c9cbcf;
  text-decoration: none;
  color: #262626;
  text-transform: uppercase;
  font-size: 1.3em;
}

.menu-item:hover {
  background: #036B52;
  color: #fefefe;
}

.load-text {
  color: #F0F2F5;
}

@media screen and (max-width: 700px) {
  .header-top {
    /* flex-direction: column; */
    padding: 0 20px;
  }


  .menu-item{
    padding: 10px;
    font-size: 1em;
  }

  .profile-name{
    font-size: .90em;
    padding: 5px;
  }

  .header-user{
    height: 30px;
    width: 150px;
  }
  
  .header-user img{
    width: 30px;
    height: 30px;
  }

}
.search-area {
  padding: 40px 0;
  display: flex;
  justify-content: center;
}

.albuns {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.albuns h3 {
  text-align: center;
  font-size: 1.4em;
  margin: 25px 0;
  text-transform: uppercase;
}

.Album {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0px 0px 16px;
  margin: 20px 2px;
  width: 290px;
  background: #ffffff;
  border: 1px solid #e1e5eb;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.06), 0px 1px 3px rgba(0, 0, 0, 0.1);
  border-radius: 6px;
}

.Album:hover {
  background: #e1e5eb;
}

.Album img {
  width: 290px;
}
.Album a {
  text-decoration: none;
  color: #262626;
}

.Album h4 {
  font-size: 20px;
  margin: 15px 10px;
  font-weight: 800;
  text-transform: uppercase;

}

.Album p {
  font-size: 16px;
  margin: 15px 10px;
}

@media screen and (max-width: 700px) {
  .search-area{
    width: 90%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 180px;
  }
}



.pretty {
  font-size: 1.6em;
}

li {
  list-style: none;
}

.music-track { 
  padding: 20px;
  margin: 20px;
  background: #ffffff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  height: 200px;
  width: 350px;

  display: flex;
  flex-wrap: nowrap;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  text-align: center;

}


.music-track:hover { 
  background: #048c6d;
  color: #fefefe
}

.c-loader-header {
  border: 5px solid #6a6a6a;
  border-top-color: #048c6d;
}

.load-text{
  color:#6a6a6a;
}

.page-album {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.list-musics {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.info-album {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 70px 0;
}

.info-album img {
  margin-right: 10px;
}

.album-text h3 {
  margin: 10px 0;
}
  
.fav-name {
  font-size: 1.8em;  
}

@media screen and (max-width: 700px) {
  .page-album{
    padding: 20px;
  }
}

.profile {
  display: flex;
  margin: 150px 0;
  justify-content: center;
  align-items: center;
}

.profile img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: 1px solid #6a6a6a;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

}

.info-perfil {
  width: 400px;
  margin-left: 30px;
  display: flex;
  flex-direction: column;
  line-height: 2;
}

.description-p {
  line-height: 1;
}

.button {
  margin-top: 40px;
  text-decoration: none;
  text-align: center;
  width: 200px;
}

@media screen and (max-width: 700px) {
  .profile{
    display: flex;
    margin: 30px 0;
    flex-direction: column;
    align-items: center;
    padding: 30px;
  }

  .info-perfil{
    width: 80%;
    margin: 20px 0;
    justify-content: center;
    align-items: center;
  }
}


.profile-edit {
  width: 40%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
}

.profile-edit label, .profile-edit input, .profile-edit textarea {
  width: 100%;
  margin: 10px 0;
}

@media screen and (max-width: 700px) {
  .profile-edit {
    margin-top: 50px;
    width: 80%;
  }
}


